import { Component, OnInit, ViewChild, TemplateRef, HostListener } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-vendor-reports',
  templateUrl: './vendor-reports.component.html',
  styleUrls: ['./vendor-reports.component.scss']
})
export class VendorReportsComponent implements OnInit {

  vendorCategoryData:any;
  vendorCategoryId:string = "";

  vendorData:string = "";
  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  modalService:any;

  vendor:any;

  vendorListData:any;

  categoryName:string = "";

  selectedInput:string = "";
  columnField: any = {};


  isVendorLoaded: boolean = false;
  allVendorListData: any;
  reportsDataList:any;
  reportsTypes: any;
  selected_report_type: any;
  reportsData:any;
  vendorList: any;

  public columnData: any;
  public vendorTableList: Array<any> = [];

  @ViewChild("viewVendorRef", { static: false }) viewVendorRef: TemplateRef<any>;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;


  constructor(
    public dialog: MatDialog,
    private vendorService:VendorService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }

  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showVendorInfo(vendor){
    console.log("working", vendor);
    this.dialog.open(this.viewVendorRef, {
        panelClass: 'material',
        disableClose: true,
        data: vendor
    });

  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  selectReport(type){
    this.vendorList = []
    this.selected_report_type = type;
    if(type == 'Active'){
      this.vendorList = this.allVendorListData.filter(item => {
        return item.isActive;
      });
    }else{
      this.vendorList = this.allVendorListData.filter(item => {
        return (item.isActive == false);
      });
    }
    let arrangeVendorRawData = {
      localdata: this.vendorList,
      datatype: "array"
    }
    this.vendorTableList = new jqx.dataAdapter(arrangeVendorRawData); 

    this.totalItems = this.vendorList.length;

    if (this.totalItems > this.itemLimit) {
      this.ItemEndIndex = this.itemLimit;
    }
    else {
      this.ItemEndIndex = this.totalItems;
    }
  }

  onGlSearchFilter() {

    if (this.vendorData != "") {

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.vendorData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if (item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();

    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'vendorReports');
  }

  @HostListener('window:onViewVendor', ['$event.detail'])
  onViewVendor(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.dialog.open(this.viewVendorRef, {
      panelClass: 'material',
      disableClose: true,
      data: dataRecord
    });
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: 'Vendor ID',
      datafield: 'vendorId',
      minwidth: 130,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Contact Person',
      datafield: 'contactPerson',
      minwidth: 250,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Phone No',
      datafield: 'phone1',
      minwidth: 170,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'VAT',
      datafield: 'vatorTin',
      minwidth: 160,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Percentage Tax',
      datafield: 'tax1',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'With Holding Tax',
      datafield: 'tax2',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      minwidth: 150,
      cellclassname: 'action-cell',
      cellsrenderer: (row : number, column, value): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="viewVendor(' + row + ')"><i class="fa fa-eye icon view" aria-hidden="true"></i></div>'
      },
      renderer: columnrenderer
    }];

    this.vendor = {};
    this.vendorCategoryId = "All";
    this.vendor.categoryName = "";

    this.reportsTypes = [
    { name: 'Active Vendors', type: 'Active' },
    { name: 'Inactive Vendors', type: 'Inactive' }
    ]

  let params = {
    apartmentId: parseInt(this.cookieService.get('apartmentId'))
  }

  this.vendorService.getVendorByApartmentId(params).subscribe((res: any) => {

    this.allVendorListData = res
    this.isVendorLoaded = true;
    },
    error => {

    });

  let vendorParams = {
    LookupTypeId: 57
  }

  this.lookupService.getLookupValueByLookupTypeId(vendorParams).subscribe((res: any) => {

    this.vendorCategoryData = res.filter(item => {
      return item.isActive;
    });
  });

    }
  }

  function viewVendor(row) {
    var event = new CustomEvent('onViewVendor', {
      detail: {
        rowId: row
      }
    })
    window.dispatchEvent(event);
  }
  (window as any).viewVendor = viewVendor;